<template>
  <div>
    <options-advanced-props />
    <el-row>
      <el-col :span="8">
        <el-form-item label="Disabled">
          <el-switch v-model="activeForm.disabled" />
        </el-form-item>
      </el-col>

      <el-col :span="8">
        <el-form-item label="Clearable">
          <el-switch v-model="activeForm.clearable" />
        </el-form-item>
      </el-col>

      <el-col :span="8">
        <el-form-item label="Multiple">
          <el-switch v-model="activeForm.multiple" />
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="Filterable">
          <el-switch v-model="activeForm.filterable" />
        </el-form-item>
      </el-col>

      <el-col :span="8">
        <el-form-item label="Remote Search">
          <el-switch
            v-model="activeForm.remote"
            @change="remoteChange"
          />
        </el-form-item>
      </el-col>

      <el-col :span="8">
        <el-form-item label="Multiple">
          <el-switch v-model="activeForm.multiple" />
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import OptionsAdvancedProps from './OptionsAdvancedProps';

export default {
    name: 'SelectListAdvancedProp',
    store: ['activeForm'],
    components: {
        OptionsAdvancedProps
    },
    methods: {
        remoteChange(value) {
            if (value) {
                this.activeForm.filterable = true;
                this.activeForm.isFromUrl = true;
            }
        }
    }
};

</script>
