<template>
  <div class="main__wrapper">
    <el-container>
      <el-main :style="cssProps">
        <el-form>
          <el-row>
            <div class="wrapper--forms">
              <el-col
                v-for="(form, index) in forms"
                :key="index"
                :span="form.span"
                v-bind="form"
                class="form__group"
              >
                <!-- <label class="form__label" v-model="form.label" v-show="form.hasOwnProperty('label')">{{ form.label }}</label> -->
                <component
                  :is="form.fieldType"
                  :current-field="form"
                  class="form__field"
                />
                <!-- <small class="form__helpblock" v-model="form.helpBlockText" v-show="form.isHelpBlockVisible">
                {{ form.helpBlockText }}
              </small> -->
              </el-col>
            </div>
          </el-row>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import {
    FormBuilder
} from '@/components/form_elements/formbuilder';
import {mapGetters} from "vuex";

export default {
    name: 'Publish',
    // store: ['forms', 'themingVars'],
    components: FormBuilder.$options.components,
    computed: {
        ...mapGetters({
            forms: 'formBuilder/getElements'
        }),
        cssProps() {
            // Return an object that will generate css properties key
            // to match with the themingVars
            //
            // Example output: { '--theme-primary-color': this.themingVars.primaryColor }
            var result = {},
                themingVars = this.themingVars;

            for (var v in themingVars) {
                if (themingVars.hasOwnProperty(v)) {
                    var newV = "--theme-" +this._.kebabCase(v),
                        suffix = "";

                    // Add px to the value if the default value contain 'px'
                    if (this._.includes(newV, 'size')) suffix = "px";
                    else if (this._.includes(newV, 'margin')) suffix = "px";
                    else if (this._.includes(newV, 'radius')) suffix = "px";

                    result[newV] = themingVars[v] + suffix;
                }
            }

            return result;
        }
    }
};
</script>

<style>
.main__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>
