import {fetchInputValues, saveInputValues} from "@/api/formInputValues";

export default {
    namespaced: true,
    state: {
        list: [],
        errors: []
    },
    getters: {
        getFormValuesByGroup: (state)=>group=>state.list.filter(el=>el?.input?.formGroup===group),
        getIndexByInput: (state)=>({id,fieldType})=>state.list.findIndex(el=>el.input.fieldType===fieldType && el.input.id===id),
        getValueByInput: (state)=>({fieldId,fieldType})=>state.list.find(el=>el.input.fieldType===fieldType && el.input.id===fieldId),
        getAllValues: (state)=>state.list,
        getAllErrors: (state)=>state.errors
    },
    actions: {
        setFormValue: ({commit,getters}, payload)=> {

            const index=getters['getIndexByInput'](payload.input);

            if(index === -1)
                commit('addFormValue', payload);
            else{
                commit('setFormValue', {index,...payload});
            }
        },
        async saveInputValues({state: {list},rootGetters,commit}){
            const step=rootGetters['formBuilder/getCurrentStep'];

            try{
                return await saveInputValues(list.filter(el=>Number(el.input.step)===Number(step)));
            }catch ({response: {data}}){
                commit("setFormErrors",data);
                throw "Error";

            }
        },
        fetchFormValues: async ({commit})=> {

            const {data}=await fetchInputValues();

            commit("setFormValues",data);
        }

    },
    mutations: {
        setFormValue: (state,{value,index})=> {
            state.list[index].value=value;
        },
        setFormErrors: (state,payload)=> {
            state.errors=payload;
        },
        addFormValue: (state,payload)=> {
            state.list.push(payload);
        },
        setFormValues: (state,payload)=> state.list=payload
    }
};