          <text-input-advanced-props v-if="activeForm.fieldType === 'TextInput'" />
          <html-advanced-props v-if="activeForm.fieldType === 'HtmlComponent'" />
          <number-input-advanced-props v-if="activeForm.fieldType === 'NumberInput'" />
          <select-list-advanced-props v-if="activeForm.fieldType === 'SelectList'" />
