<script>
import RatingAdvancedProps from './RatingAdvancedProps';
import TextInputAdvancedProps from './TextInputAdvancedProps.vue';
import HtmlAdvancedProps from './HtmlAdvancedProps.vue';
import NumberInputAdvancedProps from './NumberInputAdvancedProps.vue';
import OptionsAdvancedProps from './OptionsAdvancedProps.vue';
import SelectListAdvancedProps from './SelectListAdvancedProps.vue';
import {mapGetters,mapActions} from 'vuex';
import {cloneDeep} from 'lodash';
import formGroup from "@/store/formGroup.json";
import validationErrors from "@/backend/constants/validationErrors";

export default {
    name: 'Properties',
    components: {
        RatingAdvancedProps,
        TextInputAdvancedProps,
        HtmlAdvancedProps,
        NumberInputAdvancedProps,
        OptionsAdvancedProps,
        SelectListAdvancedProps
    },
    data() {
        return {
            formGroup,
            activeForm: {},
            labelPosition: 'top',
            fieldProperties: {},
            rules: {},
            advancedPropsVisible: false,
            validationPatterns: [
                {label: "None",pattern: ""},
                ...validationErrors.filter(e=>e.pattern)
            ],
            dialogRules: {
                dataUrl: [
                    { required: true, message: 'Please input url', trigger: 'change' }
                ]
            }
        };
    },
    computed: mapGetters({
        activeElement: 'formBuilder/getSelectedElement',
        selectedIndex: 'formBuilder/getSelectedIndex'
    }),
    watch: {
        activeForm: {
            handler(value){
                if(JSON.stringify(value)!==JSON.stringify(this.activeElement))
                    this.updateElement({
                        index: this.selectedIndex,
                        element: cloneDeep(value)
                    });
            },
            deep: true
        },
        selectedIndex: {
            handler(){
                this.activeForm=cloneDeep(this.activeElement);
                if(!this.activeForm){
                    this.activeForm={};
                }
            },
            immediate: true
        }
    },
    methods: {
        ...mapActions({
            updateElement: "formBuilder/updateElement"
        }),
        deleteOption(option, index) {
            this.$delete(option, index);
        },
        addOption(option) {
            let count = option.length + 1;

            option.push({
                optionLabel: "Option Label " + count,
                optionValue: "Option " + count
            });
        },
        addSubOption(options,index) {
            let diff=1;

            for(;diff+index<options.length;diff++){
                if(!options[diff+index].hidden){
                    break;
                }
            }
            options.splice(index+diff,0,{
                optionLabel: options[index].optionLabel+'.'+diff ,
                optionValue: options[index].optionValue+'.'+diff ,
                hidden: true
            });
        },
        addItem(item) {
            item.push({
                url: ''
            });
        },
        deleteColumn(column, index, prop) {
            this.$delete(column, index);
            this.activeForm.tableDatas.forEach(function(ele) {
                delete ele[prop];
            });
        },
        addColumn(tableColumns) {
            tableColumns.push({
                prop: '',
                label: '',
                width: 180
            });
        },
        columnWidth(value) {
            if (value && isNaN(value)) {
                this.$message.error('Column width should be a number!');
            }
        },
        confirmForm() {
            this.$refs['OptionsForm'].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    return false;
                }
            });
        }
    }
};
</script>

