<template>
  <el-row :gutter="20">
    <el-col :span="17">
      <div
        v-for="group in formGroup"
        :key="group.value"
      >
        <div
          v-if="getByGroup(group.value).length"
          class="card grid-content"
        >
          <h3 class="card-title">
            {{ group.label }}
          </h3>
          <el-row
            v-for="(record,i) in getByGroup(group.value)"
            :key="i"
            :gutter="0"
          >
            <el-col :span="10">
              <p class="card-question">
                {{ record.input.label }}
              </p>
            </el-col>
            <el-col
              :span="13"
              :offset="1"
            >
              <p class="card-answer">
                {{ record.value }}
              </p>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-col>
    <el-col :span="7">
      <div class="card grid-content">
        <div class="card-profile-picture">
          <img
            class="profile-picture"
            :src="getAvatar()"
          >
        </div>
      </div>
      <div class="card grid-content">
        <h3 class="card-title">
          Documents
        </h3>
        <div class="card-container-icons">
          <div
            v-for="document in getDocuments()"
            :key="document"
            class="card-container-icon"
          >
            <a
              :href="document"
            >
              <img
                src="/document.png"
                width="50"
              >
              {{ document }}
            </a>
          </div>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import  {mapGetters} from "vuex";
import formGroup from "@/store/formGroup.json";
import isImage from "@/utils/isImage";

export default {
    data() {
        return {
            formGroup: formGroup.filter(el=>el.value!='documents')
        };
    },
    computed: mapGetters({
        getAllValues: 'formValues/getAllValues',
        getByGroup: 'formValues/getFormValuesByGroup'
    }),
    methods: {
        getAvatar(){

            let avatar=this.getAllValues.find(e=>
                e?.input?.fieldType==='Upload'&&
                isImage(e.value)
            );

            if(avatar)
                return avatar.value;
            else
                return '/no_avatar.png';
        },
        getDocuments(){
            const avatar=this.getAvatar();

            return this.getAllValues.filter(e=> e.value!==avatar&&e?.input?.fieldType==='Upload').map(e=>e.value?.replace('uploads/',''));

        }
    }
};
</script>
<style scoped>
.card-container-icon{
  display: block;
  width: 125px;
  font-size: 10px;
  text-align: center
}
</style>
