<template>
  <div id="app">
    <el-tabs
      v-if="!isLoading"
      v-model="activeName"
    >
      <el-tab-pane
        label="Design"
        name="Design"
      >
        <home />
      </el-tab-pane>
      <el-tab-pane
        label="Preview"
        name="Preview"
      >
        <preview />
      </el-tab-pane>
      <el-tab-pane
        label="Overview"
        name="Overview"
      >
        <overview />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Home from '@/views/Home';
import Preview from '@/views/Preview';
import Overview from '@/views/Overview';
import {mapActions,mapGetters} from "vuex";

export default {
    name: 'App',
    components: {
        Home,
        Preview,
        Overview
    },
    data() {
        return {
            activeName: "Design"
        };
    },
    watch: {
        activeName(){this.fetchData();}
    },
    created() {
        this.fetchData();
    },
    computed: {
        ...mapGetters({
            isLoading: 'formBuilder/getIsLoading'
        })
    },
    methods: {
        ...mapActions({
            fetchElements: 'formBuilder/fetchElements'
        }),
        async fetchData(){
            await this.fetchElements();
        }
    }
};
</script>
