import formTypes from './formTypes.json';
import {cloneDeep} from 'lodash';
import {deleteFormElement, getFormElements, updateFormBuilder} from "@/api/formBuilder";

export default {
    namespaced: true,
    state: {
        formTypes: formTypes.fields,
        elements: [],
        selectedIndex: null,
        currentStep: 0,
        isLoading: false
    },
    getters: {
        getIsLoading: state=>state.isLoading,
        getFormTypes: state=>state.formTypes,
        getSelectedIndex: state=>state.selectedIndex,
        getCurrentStep: state=>state.currentStep,
        getStepsCount: state=>state.elements.find(el=>el.fieldType==='Steps')?.options.length || 0,
        getElements: state=>state.elements,
        getSelectedElement: state=>state.elements[state.selectedIndex]
    },
    actions: {
        addElement: ({commit,dispatch},element)=> {
            commit('addElement', cloneDeep(element));
            return dispatch('saveElements');
        },
        selectElement: ({commit},index)=> {
            commit('selectElement', index);
        },
        updateElements: ({commit,dispatch},elements)=> {
            commit('updateElements', elements);
            commit('selectElement', null);
            return dispatch('saveElements');
        },
        deleteElement: async ({commit,getters},index)=> {
            const newElements=cloneDeep(getters['getElements']);

            await deleteFormElement(newElements[index].fieldType,newElements[index].id);

            newElements.splice(index,1);
            commit('updateElements', newElements);
            commit('selectElement', null);
        },
        updateElement: ({commit,dispatch}, {index, element}) => {
            commit('updateElement', {index,element});
            return dispatch('saveElements');
        },
        fetchElements: async ({commit,dispatch,state: {currentStep}})=> {
            commit('updateLoading',true);
            const {data}=await getFormElements(currentStep);

            commit('updateElements',data);
            await dispatch('formValues/fetchFormValues',{}, {root: true});

            commit('updateLoading',false);
        },
        setCurrentStep: async ({commit,dispatch,getters},step)=>{
            try {
                if(step>getters['getCurrentStep']){
                    await dispatch('formValues/saveInputValues', {},{root: true});
                }
                commit('updateCurrentStep',step);
                await dispatch('fetchElements');
            }catch (e) {
                console.error(e);
            }
        },
        saveElements: async ({commit,state})=> {
            commit('updateElements',
                (await updateFormBuilder(
                    state.currentStep,
                    state.elements)
                ).data);
        }
    },

    mutations: {
        updateCurrentStep: (state, step) => state.currentStep=step,
        addElement: (state, element) => state.elements.push(element),
        selectElement: (state, index) =>state.selectedIndex=index,
        updateElements: (state, elements) =>state.elements=elements,
        updateLoading: (state, payload) =>state.isLoading=payload,
        updateElement: (state, {index,element}) =>state.elements[index]=element
    }
};
