<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-form-item label="Colors">
          <el-row :gutter="3">
            <el-col
              v-for="(color, index) in activeForm.colors"
              :key="index"
              :span="7"
            >
              <el-input v-model="activeForm.colors[index]" />
            </el-col>
          </el-row>
        </el-form-item>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="12">
        <el-form-item label="Show Text">
          <el-switch
            v-model="activeForm.showText"
            @change="showTextChange"
          />
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="Disabled">
          <el-switch v-model="activeForm.disabled" />
        </el-form-item>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="24">
        <el-form-item
          v-show="activeForm.showText"
          label="Texts"
        >
          <el-row :gutter="3">
            <el-col
              v-for="(text, index) in activeForm.texts"
              :key="index"
              :span="4"
            >
              <el-input v-model="activeForm.texts[index]" />
            </el-col>
          </el-row>
        </el-form-item>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="12">
        <el-form-item label="Show Score">
          <el-switch
            v-model="activeForm.showScore"
            @change="showScoreChange"
          />
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item
          v-show="activeForm.showScore"
          label="Score Unit"
        >
          <el-input v-model="activeForm.scoreUnit" />
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>
<script>

export default {
    name: 'RatingAdvancedProp',
    store: ['activeForm'],
    data() {
        return {
            name: ''
        };
    },
    methods: {
        showTextChange(value) {
            if (value && this.activeForm.showScore) {
                this.activeForm.showScore = false;
            }
        },
        showScoreChange(value) {
            if (value && this.activeForm.showText) {
                this.activeForm.showText = false;
            }
        }
    }
};

</script>

