<template>
  <el-form-item

    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
    :required="currentField.isRequired"
    :error="error"
  >
    <el-input
      v-model="value"
      v-bind="attributesBinding"
    >
      <template
        v-if="currentField.prepend !== ''"
        slot="prepend"
      >
        {{ currentField.prepend }}
      </template>
      <template
        v-if="currentField.append !== ''"
        slot="append"
      >
        {{ currentField.append }}
      </template>
    </el-input>
  </el-form-item>
</template>
 
<script>
import FormElementInput from "@/mixins/FormElementInput";
import FormElementError from "@/mixins/FormElementError";


export default {
    name: 'TextInput',
    mixins: [
        FormElementInput(),
        FormElementError()
    ],
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    },
    data() {
        return {
            value: '',
            error: ""
        };
    },
    computed: {
        attributesBinding() {
            let attr = {};

            if (this.currentField.showWordLimit) {
                attr.maxlength = this.currentField.maxlength;
                attr['show-word-limit'] = this.currentField.showWordLimit;
            }

            if (this.currentField.clearable) {
                attr.clearable = this.currentField.clearable;
            }

            if (this.currentField.disabled) {
                attr.disabled = this.currentField.disabled;
            }

            if (this.currentField.showPassword) {
                attr['show-password'] = this.currentField.showPassword;
            }

            if (this.currentField.isPlaceholderVisible) {
                attr.placeholder = this.currentField.placeholder;
            }

            return attr;
        }
    }
};
</script>
