<template>
  <el-form-item
    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
    :error="error"
  >
    <el-input-number
      v-model="value"
      :step="1"
      controls-position="right"
      v-bind="attributesBinding"
    />
  </el-form-item>
</template>


<script>
import FormElementInput from "@/mixins/FormElementInput";
import FormElementError from "@/mixins/FormElementError";


export default {
    name: 'NumberInput',
    mixins: [
        FormElementInput(),
        FormElementError('error','value')

    ],
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    },
    data() {
        return {
            value: 0,
            error: ""
        };
    },
    computed: {
        attributesBinding() {
            var attr = {};

            attr.disabled = this.currentField.disabled;
            attr.step = this.currentField.step;
            attr['step-strictly'] = this.currentField.stepStrictly;
            if (this.currentField.hasMinValue) {
                attr.min = this.currentField.min;
            }
            if (this.currentField.hasMaxValue) {
                attr.max = this.currentField.max;
            }
            return attr;
        }
    }
};
</script>
