<template>
  <el-form-item
    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
    :error="error"
  >
    <el-checkbox-group v-model="checkList">
      <el-checkbox
        v-for="item in currentField.options"
        :key="item.optionValue"
        :label="item.optionLabel"
        :disabled="item.disabled"
      />
    </el-checkbox-group>
  </el-form-item>
</template>

<script>
import FormElementInput from "@/mixins/FormElementInput";
import FormElementError from "@/mixins/FormElementError";


export default {
    name: 'Checkbox',
    mixins: [FormElementInput(
        'checkList',
        e=>e.join("   &   "),
        e=>e?.split("   &   ")||[]
    ),
    FormElementError('error')
    ],
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    },
    data() {
        return {
            checkList: [],
            error: ""
        };
    }
};
</script>

<style>
.el-checkbox-group {
  font-size: 14px;
}
</style>
