import {mapGetters} from "vuex";
import rules from "@/backend/constants/validationErrors";

export default (errorFiled="error",valueField='value')=>({
    watch: {
        [valueField]: function(){
            this.validateField();
        },
        formErrors: function(){
            this.validateField();
        }
    },
    computed: {
        ...mapGetters({
            formErrors: 'formValues/getAllErrors'
        })
    },
    methods: {
        validateField(){
            if(!this.formErrors.length)return;
            for(let error of rules){
                if(!error.validator.call({
                    input: this.currentField,
                    value: this[valueField]
                }))
                {
                    this[errorFiled]= error.message;
                    return false;

                }else{
                    this[errorFiled] = "";
                }
            }
        }
    }
});