<template>
  <el-form-item :label-width="currentField.labelWidth + 'px'">
    <el-button
      v-if="currentStep"
      type="primary"
      class="form__button"
      icon="el-icon-caret-left"
      circle
      @click="setStep(currentStep-1)"
    />
    <el-button
      v-if="currentStep<stepsCount-1"
      type="primary"
      class="form__button"
      icon="el-icon-caret-right"
      circle
      @click="setStep(currentStep+1)"
    />
  </el-form-item>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';

export default {
    name: 'Arrow',
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }

    },
    computed: mapGetters({
        currentStep: 'formBuilder/getCurrentStep',
        stepsCount: 'formBuilder/getStepsCount'
    }),
    methods: {
        ...mapActions({
            setStep: 'formBuilder/setCurrentStep'
        })
    }
};
</script>
