import {mapActions, mapGetters} from "vuex";

export default (valueKey='value',
    getValue=(value)=>value,
    setValue=(value)=>value
)=> ({
    watch: {
        [valueKey]: function(){

            this.setFormValue({
                value: getValue(this[valueKey]),
                input: this.currentField
            });
        }
    },
    mounted() {
        const {fieldType,id}=this.currentField;

        const record=this.formValues({fieldType,fieldId: id});

        if(!record) {
            this.setFormValue({
                value: undefined,
                input: this.currentField
            });
        }else
            this[valueKey]=setValue(record.value);
    },
    computed: {
        ...mapGetters({
            formValues: 'formValues/getValueByInput'
        })
    },
    methods: {
        ...mapActions({
            setFormValue: 'formValues/setFormValue',
            createFormValue: 'formValues/createFormValue'
        })
    }
});