<template>
  <el-form-item
    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
  >
    <el-steps
      v-model="steps"
      :active="currentStep"
      finish-status="success"
    >
      <template v-for="item in currentField.options">
        <el-step
          v-if="!item.hidden"
          :key="item.optionValue"
          :description="item.optionLabel"
          :disabled="item.disabled"
        />
      </template>
    </el-steps>
  </el-form-item>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
    name: 'Steps',
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    },
    data() {
        return {
            steps: [4],
            active: 1
        };
    },
    computed: {
        currentStep(){
            let step=-1;

            for(let i in this.currentField.options){

                if(!this.currentField.options[i].hidden){
                    step++;
                }
                if(this.$store.getters['formBuilder/getCurrentStep']===Number(i)){
                    return step;
                }
            }
        }
    },
    mounted() {
        if (this.currentField.isFromUrl) {
            // let dataUrl = this.currentField.dataUrl;
            //
            // let valueField = this.currentField.valueField;
            //
            // let labelField = this.currentField.labelField;

            // let promise = fetchData.fetchOptionsData(dataUrl, labelField, valueField);

            // promise.then((data) => {
            //     this.currentField.options = data;
            // });
        }
    }
};
</script>

<style>
.el-checkbox-group {
    font-size: 10px;
}
</style>
