<template>
  <div>
    <el-container v-if="!isLoading">
      <el-main>
        <div class="wrapper--forms">
          <el-form>
            <el-row>
              <draggable
                :value="forms"
                class="dragArea"
                :group="{ name:'formbuilder', pull:false, put:true }"
                :sort="true"
                ghost-class="sortable__ghost"
                @change="onFormChaged"
              >
                <!-- The form elements starts (on the right) -->
                <!-- <div> -->
                <el-col
                  v-for="(form, index) in forms"
                  :key="index"
                  v-bind="form"
                  :span="form.span"
                  class="form__group"
                  :class="{ 'is--active': index === selectedElementIndex }"
                >
                  <span class="form__selectedlabel">{{ form.fieldType }}</span>
                  <div @click="editElementProperties(index)">
                    <!-- <label class="form__label" v-model="form.label" v-show="form.hasOwnProperty('label')">{{ form.label }}</label> -->
                    <component
                      :is="form.fieldType"
                      :current-field="form"
                      class="form__field"
                    />
                  </div>

                  <!-- Actions list -->
                  <div class="form__actiongroup">
                    <el-button
                      circle
                      size="mini"
                      icon="el-icon-rank"
                      class="form__actionitem--move"
                    />

                    <el-button-group class="form__actionlist">
                      <el-button
                        v-show="!form.isUnique"
                        size="mini"
                        icon="el-icon-plus"
                        @click="cloneElement(index, form)"
                      />
                      <el-button
                        size="mini"
                        icon="el-icon-delete"
                        @click="deleteElement(index)"
                      />
                    </el-button-group>
                  </div>
                </el-col>
              </draggable>
            </el-row>
          </el-form>
        </div>
      </el-main>
      <el-aside
        class="wrapper--sidebar"
        width="30%"
      >
        <el-tabs
          v-model="activeTabForFields"
          type="border-card"
        >
          <el-tab-pane
            name="elements"
            label="Elements"
          >
            <Elements />
          </el-tab-pane>

          <el-tab-pane
            v-if="selectedElementIndex!==null"
            name="properties"
            label="Properties"
          >
            <Properties />
          </el-tab-pane>
        </el-tabs>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import {
    FormBuilder
} from '@/components/form_elements/formbuilder';
import Elements from '@/components/form_elements/Elements';
import Properties from '@/components/form_elements/properties/Properties';

import {cloneDeep} from 'lodash';
import {mapGetters,mapActions} from 'vuex';

export default {
    name: 'Home',
    components: {
        ...FormBuilder.$options.components,
        Elements,
        Properties
    },
    data() {
        return {
            isLoading: false,
            activeTabForFields: 'elements',
            sortElementOptions: FormBuilder.$data.sortElementOptions
        };
    },
    computed: {
        ...mapGetters({
            forms: "formBuilder/getElements",
            selectedElementIndex: "formBuilder/getSelectedIndex"
        })
    },
    watch: {
        selectedElementIndex(value){
            if(value!==null){
                this.activeTabForFields='properties';
            }else {
                this.activeTabForFields='elements';
            }
        }
    },
    methods: {
        ...mapActions({
            updateElements: 'formBuilder/updateElements',
            deleteElement: 'formBuilder/deleteElement',
            selectElement: 'formBuilder/selectElement',
            fetchElements: 'formBuilder/fetchElements',
            fetchValues: 'formValues/fetchFormValues'
        }),
        cloneElement(index) {

            const newElements=cloneDeep(this.forms);

            newElements.splice(index,0,newElements[index]);
            this.updateElements(newElements);
        },
        editElementProperties(index) {
            this.selectElement(index);
        },
        onFormChaged(e){
            const newElements=cloneDeep(this.forms);

            if(e.moved){
                newElements[e.moved.oldIndex] = newElements.splice(e.moved.newIndex, 1, newElements[e.moved.oldIndex])[0];
                this.updateElements(newElements);
            }
        }
    }
};
</script>

