<template>
  <el-form-item
    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
    :error="error"
  >
    <el-upload
      class="avatar-uploader"

      action="/api/form-values/upload"
      :show-file-list="false"
      :on-preview="handlePreview"
      :file-list="fileList"
      :on-success="handleSuccess"
    >
      <img
        v-if="imageUrl"
        :src="getImageUrl()"
        class="avatar"
        alt="avatar"
      >
      <i
        v-else
        class="el-icon-plus avatar-uploader-icon"
      />
    </el-upload>
  </el-form-item>
</template>

<script>
import FormElementInput from '@/mixins/FormElementInput';
import FormElementError from '@/mixins/FormElementError';
import isImage from "@/utils/isImage";

export default {
    name: 'Upload',
    mixins: [
        FormElementInput("imageUrl"),
        FormElementError()
    ],
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    }, 
    data() {
        return {
            imageUrl: "",
            error: "",
            fileList: []
        };
    },
    methods: {
        handleSuccess({path}){
            this.imageUrl=`${path}`;
        },
        handlePreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        getImageUrl(){
            return isImage(this.imageUrl) ? `/${this.imageUrl}` : '/document.png';
        }
    }
};
</script>
<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  max-width: 178px;
  max-height: 178px;
  display: block;
}
</style>