<template>
  <div>
    <el-row>
      <el-col :span="8">
        <el-form-item label="Password">
          <el-switch
            v-model="activeForm.showPassword"
            @change="passwordChange"
          />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="Disabled">
          <el-switch v-model="activeForm.disabled" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="Clearable">
          <el-switch v-model="activeForm.clearable" />
        </el-form-item>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="12">
        <el-form-item label="Prepend">
          <el-input v-model="activeForm.prepend" />
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="Append">
          <el-input v-model="activeForm.append" />
        </el-form-item>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="12">
        <el-form-item label="Show Word Limit">
          <el-switch
            v-model="activeForm.showWordLimit"
            @change="showWordLimitChange"
          />
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item
          v-show="activeForm.showWordLimit"
          label="Word Limit"
        >
          <el-input-number
            v-model="activeForm.maxlength"
            :min="1"
          />
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
    name: 'RatingAdvancedProp',
    store: ['activeForm'],
    data() {
        return {
            name: ''
        };
    },
    methods: {
        passwordChange(value) {
            if (value) {
                this.activeForm.showWordLimit = false;
            }
        },
        showWordLimitChange(value) {
            if (value) {
                this.activeForm.showPassword = false;
            }
        }
    }
};
</script>
