<template>
  <el-form-item
    :label="currentField.label"
    :label-width="currentField.labelWidth + 'px'"
  >
    <el-switch
      v-model="value"
      :active-text="currentField.activeText"
      :inactive-text="currentField.inActiveText"
    />
  </el-form-item>
</template>


<script>
import FormElementInput from "@/mixins/FormElementInput";

export default {
    name: 'Elswitch',
    mixins: [FormElementInput(
        "value",
        (e)=>e,
        el=>!!Number(el)
    )],
    props: {
        currentField: {
            type: Object,
            required: false,
            default: ()=>({})
        }
    },
    data() {
        return {
            value: true
        };
    }
};
</script>
